class WFSnav {
    constructor(config){
        this.config = config;
        this.numberOptions = ['duration', 'marginNav', 'showDown', 'showUp', 'mobileFinish', 'mobileMarginNav'];
        this.dataAttrbox = `data-nav`;
        this.dataAttrlink = `data-nav-link`;
        this.options = {
            conteiner: '#header',
            activeClass: 'active',
            activeClassElement: 'elements',
            elements: '> li > a',
            duration: 1300,
            typePath: '#',
            navHeight: true,
            marginNav: 0,
            showDown: 30,
            showUp: 50,
            mobileFinish: 992,
            mobileNavHeight: false,
            mobileMarginNav: 0,
            mobileCloseOpenEl: '.close-momile-nav',
            mobileCloseCl: 'open-mobile-menu'
        }
        
        if(typeof config === 'object'){
            this.options = {...this.options, ...config}
        }

        this.init();
    }
    
    init(){
        this.#validateOption();
    }

    #validateOption(){
        this.flagValidation = true;

        this.numberOptions.forEach(numberOption => {
            if(!Number.isFinite(this.options[numberOption]) && !Number.isNaN(this.options[numberOption]) ) {
                alert(`WFSnav ${numberOption} = ${this.options[numberOption]} - should be a number`)
                this.flagValidation = false;
            }
        });

        if(this.flagValidation){
            
            this.conteiner = document.querySelector(this.options.conteiner);

            if(this.conteiner){
                this.WFSgotoBody = document.body;
                this.elements = this.conteiner.querySelectorAll(this.options.elements);
                this.navLinkLength = this.elements.length;
                this.navLinkCount = this.navLinkLength - 1;

                if(this.navLinkLength > 0){
                    console.log(`!!! START - WFSnav !!!`);
                    this.windowHeight = window.innerHeight;
                    this.bodyHeight = this.WFSgotoBody.offsetHeight;
                    this.conteinerHight = this.conteiner.offsetHeight;
                    this.closeOpenLink = this.WFSgotoBody.querySelectorAll(this.options.mobileCloseOpenEl);
                    this.activeFlag = false;
    
                    if(this.closeOpenLink.length > 0) this.#createIventOpenClose();
                    this.#checkMobile();
                    this.#setupGoto();
                    this.allBox = [];
    
                    Array.from(this.elements).forEach((element, i) => {
                        this.allBox.push(this.WFSgotoBody.querySelector(element.getAttribute('href')))
                    });
    
                    this.#scrollWindow();
                    this.#resizeWindow();
                    this.#actionOnScroll();
                }else {
                    console.log(`!!! WFSnav : no items found !!!`);
                }
            }
        }
    }

    #scrollWindowTo(top){
        window.scrollTo({
            top: top,
            left: 0,
            behavior: "smooth"
        });
    }

    #checkMobile(){
        this.windowWidth = window.innerWidth;
        let space1 = 0; 
        let space2 = 0;
        if(this.windowWidth < this.options.mobileFinish){
            this.mobile = true;
            if(this.options.mobileNavHeight){
                space1 = this.conteinerHight;
            }

            space2 = this.options.mobileMarginNav;
        }
        else {
            this.mobile = false;
            if(this.options.navHeight){
                space1 = this.conteinerHight;
            }

            space2 = this.options.marginNav;
        }

        this.space = space1 + space2;

        return [{mobile: this.mobile},{windowWidth: this.windowWidth}]
    }

    #controllClass(){
        if (typeof this.avtiveElement != undefined && this.avtiveElement != null) {
            if(!this.avtiveElement.classList.contains(this.options.activeClass)){
                this.elements.forEach(element => {
                    element.classList.remove(this.options.activeClass);
                });
                this.avtiveElement.classList.add(this.options.activeClass);
                this.activeFlag = true;
            }
        }else {
            if(this.activeFlag){
                this.#removeClassActive();
            }
        }
    }

    #removeClassActive(){
        this.elements.forEach(element => {
            element.classList.remove(this.options.activeClass);
        });
        this.activeFlag = false;
    }

    #actionOnScroll = () => {
        this.positionTop = window.scrollY;
        this.scrollFlag = (this.positionTop >= this.lastScrollTop) ? true : false;
        this.lastScrollTop = this.positionTop;
        this.visibleFirst = false;
        this.notViseble = true;

        this.allBox.forEach((element, i) => {
            this.goToBoxPosition = element.getBoundingClientRect().y + window.scrollY - this.space;
            this.windowHeight = window.innerHeight;
            this.bodyHeight = this.WFSgotoBody.offsetHeight;

            if(this.scrollFlag){
                if (this.goToBoxPosition - this.space - (element.getBoundingClientRect().height/ (100/this.options.showDown)) <= this.positionTop && this.positionTop + this.windowHeight > this.goToBoxPosition) {
                    this.avtiveElement = document.querySelector(`[${this.dataAttrlink}="${element.getAttribute(this.dataAttrbox)}"]`);
                    this.avtiveElementCount = i;
                }
            }else {
                if(!this.visibleFirst){
                    if (this.goToBoxPosition  <= this.positionTop && this.goToBoxPosition + this.space + (element.getBoundingClientRect().height / (100/this.options.showUp)) >= this.positionTop) {
                        this.avtiveElement = document.querySelector(`[${this.dataAttrlink}="${element.getAttribute(this.dataAttrbox)}"]`);
                        this.avtiveElementCount = i;
                        this.visibleFirst = true;
                    }else {
                        if(this.positionTop <= this.goToBoxPosition && this.positionTop + this.windowHeight > this.goToBoxPosition){
                            this.avtiveElement = document.querySelector(`[${this.dataAttrlink}="${element.getAttribute(this.dataAttrbox)}"]`);
                            this.avtiveElementCount = i;
                            this.visibleFirst = true;
                        }
                    }

                }
            }

            if(this.avtiveElementCount == 0){
                if(this.allBox[0].getBoundingClientRect().y >= this.windowHeight - this.space - (this.allBox[0].getBoundingClientRect().height/ (100/this.options.showDown))){
                    this.avtiveElement = null
                }
            }

            if(this.avtiveElementCount == this.navLinkCount){
                if(this.allBox[this.navLinkCount].getBoundingClientRect().y + this.allBox[this.navLinkCount].getBoundingClientRect().height < 0){
                    this.avtiveElement = null
                }
            }

            this.#controllClass();
        });
    }

    onScroll(){
        const wfsNavArry = [
            {navElements: this.elements},
            {navLinkLength: this.navLinkLength},
            {avtiveElement: this.avtiveElement},
            {avtiveElementCount: this.avtiveElementCount},
            {sections: this.allBox},
        ];

        return wfsNavArry;
    }

    #scrollWindow(){
        window.addEventListener('scroll', this.#actionOnScroll)
    }

    #resizeWindowFunction = () => {
        const _this = this;
        let _time;
        if(_time) clearTimeout(_time);
        _time = setTimeout(function(){
            _this.windowHeight = window.innerHeight;
            _this.bodyHeight = _this.WFSgotoBody.clientHeight;

            _this.#checkMobile();
        }, 500);
    }

    #resizeWindow(){
        window.addEventListener('resize', this.#resizeWindowFunction);
    }

    #fingIdElement(trigger){
        this.attrElement = trigger.getAttribute('href');
        this.fPrefix = this.attrElement.substring(0, this.options.typePath.length);
        this.elId = this.attrElement.slice(this.options.typePath.length);
    }

    #scrollToBox(trigger){
        this.#fingIdElement(trigger);
        
        if(this.fPrefix === this.options.typePath){
            this.goToBox = document.getElementById(this.elId);
            this.#scrollWindowTo(this.goToBox.getBoundingClientRect().y + window.scrollY - this.space);
        }
    }

    #clickElement(element, event){
        event.preventDefault();
        this.#scrollToBox(element);
        this.closeMobileNav(); 
    }

    #setupGoto(){
        this.listenersElement = new Map();

        Array.from(this.elements).forEach((element, i) => {
            this.#fingIdElement(element);
            document.getElementById(this.elId).setAttribute(this.dataAttrbox, this.elId);
            element.setAttribute(this.dataAttrlink, this.elId);

            const handleClick = (event) => this.#clickElement(element, event);
			this.listenersElement.set(element, handleClick);
			element.addEventListener('click', handleClick);
        })
    }

    // START CLOSE ------------------------------------------------------------------

    closeMobileNav(){
        this.WFSgotoBody.classList.remove(this.options.mobileCloseCl)
    }

    openMobileNav(){
        this.WFSgotoBody.classList.add(this.options.mobileCloseCl)
    }

    #clickOpenClose(event){
        event.preventDefault();

        if(!this.WFSgotoBody.classList.contains(this.options.mobileCloseCl)){
            this.openMobileNav();
        }else{
            this.closeMobileNav();
        }
    }

    #createIventOpenClose(){
        this.listeners = new Map();

        Array.from(this.closeOpenLink).forEach((element, i) => {
            const handleClick = (event) => this.#clickOpenClose(event);
            this.listeners.set(element, handleClick);
            element.addEventListener('click', handleClick);
        });
    }

    #destroyIventOpenClose(){
        this.closeMobileNav();
		Array.from(this.closeOpenLink).forEach((element, i) => {
			const handleClick = this.listeners.get(element);
			if (handleClick) {
				element.removeEventListener('click', handleClick);
				this.listeners.delete(element);
			}
		});
	}

    #destroyElements(){
        Array.from(this.elements).forEach((element, i) => {
            const handleClick = this.listenersElement.get(element);
            if (handleClick) {
                element.removeEventListener('click', handleClick);
                this.listenersElement.delete(element);
            }
            element.removeAttribute(this.dataAttrlink);
        });
    }

    #destroyBox(){
        Array.from(this.allBox).forEach(element => {
            element.removeAttribute(this.dataAttrbox);
        });
    }

    // END CLOSE --------------------------------------------------------------------

    destroy(){
        if(this.elements){
            this.#destroyElements();
            this.#removeClassActive();
            this.#destroyBox();
            window.removeEventListener('resize', this.#resizeWindowFunction);
            window.removeEventListener('scroll', this.#actionOnScroll);
            if(this.closeOpenLink.length) this.#destroyIventOpenClose();
        }
    }
}